.form {
  margin: 0 auto;
  padding: 40px 20px;
  width: 280px;
  height: 496px;
  background-color: #ffffff;
  box-shadow: 5px 10px 20px rgba(170, 178, 197, 0.4);
  border-radius: 30px;
}
@media screen and (min-width: 768px) {
  .form {
    width: 426px;
    height: 552px;
    margin-top: 80px;
    margin-bottom: 155px;
    padding: 56px 83px;
  }
}
@media screen and (min-width: 1280px) {
  .form {
    margin-top: 117px;
    margin-bottom: 125px;
    margin-right: 75px;
  }
}

.formTextGoogle {
  width: 200px;
  margin: 0 auto 16px;
  font-family: Roboto;
  font-style: normal;
  font-size: 12px;
  line-height: 1.17;
  text-align: center;
  letter-spacing: 0.04em;
  color: #52555f;
}

@media screen and (min-width: 768px) {
  .formTextGoogle {
    width: 100%;
    margin-bottom: 20px;
  }
}

.googleBtn {
  display: flex;
  align-items: center;
  width: 122px;
  height: 40px;
  margin: 0 auto;
  margin-bottom: 32px;
  padding: 10px 18px 10px 20px;
  filter: drop-shadow(1px 2px 3px rgba(170, 178, 197, 0.2));
  border-radius: 26px;
  border: none;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  text-decoration: none;
  cursor: pointer;
  color: #000000;
  background-color: #f6f7fb;

  transition: background-color 100ms linear;
}

.googleBtn:hover,
.googleBtn:focus {
  background-color: #ff751d;
  outline: none;
}

.googleSvg {
  margin-right: 10px;
}

.formText {
  width: 230px;
  font-family: Roboto;
  font-style: normal;
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.04em;
  color: #52555f;
}
@media screen and (min-width: 768px) {
  .formText {
    margin-bottom: 20px;
  }
}

.formBox {
  margin-top: 16px;
}
@media screen and (min-width: 768px) {
  .formBox {
    margin-top: 20px;
  }
}

.formLabel {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;
  letter-spacing: 0.04em;
  color: #000000;
}
.formLabel:first-child {
  margin-bottom: 20px;
}
@media screen and (min-width: 768px) {
  .formLabel {
    font-size: 12px;
    line-height: 1.17;
  }
  .formLabel:first-child {
    margin-bottom: 32px;
  }
}

.spanLabel {
  color: #eb5757;
}

.formInput {
  margin-top: 12px;
  padding: 17px 19px;
  border: none;
  border-radius: 30px;
  outline: none;
  background-color: #f6f7fb;
}
@media screen and (min-width: 768px) {
  .formInput {
    padding: 17px 0 17px 19px;
  }
}

.formInput::placeholder {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: #a6abb9;
}

.errorMsg {
  position: absolute;
  left: 0;
  bottom: -16px;

  font-size: 10px;
  line-height: 12px;
  letter-spacing: 0.04em;

  color: #eb5757;
}

.authBtn {
  display: flex;
  justify-content: space-between;
  width: 240px;
  margin-top: 40px;
  margin-bottom: 40px;
}
@media screen and (min-width: 768px) {
  .authBtn {
    width: 259px;
    margin-bottom: 56px;
  }
}

.btn {
  width: 116px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 16px;
  filter: drop-shadow(1px 3px 5px rgba(82, 85, 95, 0.15));
  cursor: pointer;
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.17;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  background-color: #f5f6fb;
  color: #52555f;

  background-position: 100% 0;
  background-size: 200% 200%;
  transition: 500ms;
}

@media screen and (min-width: 768px) {
  .btn {
    width: 122px;
  }
}

.btn:hover {
  background-color: #ff751d;
  color: #ffffff;
  outline: none;
  transition: 0.1s linear;
}
